    .mobile {
        &_item {
            width: 290px;
            &_subtitle {
                font-size: 22px; } } }
    .footer {
        &_wrapper>div {
            width: 50%;
            &:nth-child(1) {
                width: 25%; }
            &:nth-child(3) {
                width: 25%;
                display: flex;
                justify-content: flex-end; } } } }


@media(max-width: 991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_phone {
            font-size: 17px; } }
    .button_call {
        margin-top: 21px;
        height: 40px;
        width: 100%; }
    .request {
        &_h1 {
            font-size: 40px; }
        &_h2 {
            font-size: 30px; }
        &_button {
            display: block;
            width: 200px;
            height: 50px;
            font-size: 15px;
            margin: 100px 250px 0 250px; } }
    .reasons {
