@media(max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }

        &_item {
            padding-right: 0;
            border: none;
            margin-top: 10px; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px; }
            &:nth-last-child(1) {
                transform: translateY(3px) rotate(-45deg); }
            &:nth-last-child(2) {
                display: none; }
            &:nth-last-child(3) {
                transform: translateY(3px) rotate(45deg); } } }


    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            font-size: 12px;
            text-align: center;
            color: #fff;
            margin-top: 10px;
            margin-left: 51%; }
        .button_call {
            display: block;
            margin: 0 auto;
            margin-top: 5px;
            margin-left: 20%;
            width: 200px;
            height: 40px; } }
    .mobile {
        padding: 40px 0; }
    .choise {
        min-height: 600px;
        padding: 40px 0; }
    .footer {
        min-height: 245px;
        width: 100%;

        &_blueline {
            width: 253px;
            height: 4px;
            background-color: #1fbad6;
            margin: 0 auto; }
        &_wrapper {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            padding-top: 32px; }
        &_wrapper>div {
            width: 100%;
            &:nth-child(3) {
                height: 32px;
                width: 100%;
                display: flex;
                justify-content: space-between; } }
        &_menu {
            width: 100%;
            margin: 0 auto;
            margin-top: 22px;

            &_main {
                width: 100%;
                display: flex;
                justify-content: space-between;
                padding: 0 10px;
                a {
                    font-size: 11px;
                    font-weight: 300;
                    text-transform: uppercase;
                    text-align: center;
                    color: #070716; } }

            &_submenu {
                width: 100%;
                display: flex;
                justify-content: space-between;
                margin-top: 10px;
                a {
                    text-transform: uppercase;
                    font-size: 8px;
                    font-weight: 300;
                    text-align: left;
                    color: #999; } }
            &_lang {
                display: block;
                margin: 0 auto;
                margin-top: 13px;
                font-size: 10px;
                font-weight: 400;
                text-align: center;
                color: #1fbad6;
                text-transform: uppercase; }

            &_social {
                width: 100%;
                display: flex;
                margin: 0;
                align-items: center;
                justify-content: space-between;

                .social_logo {
                    width: 32px;
                    height: 32px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: #000;
                    margin: 0 auto;
                    border-radius: 100%; } } } } }

